                    text: "Мы работаем на рынке GPS мониторинга с 2017, чтобы владелец бизнеса мог в любой момент времени знать местонахождение своего автопарка и сотрудников благодаря GPS и GSM технологиям.",
                },
                {
                    icon: require("@/assets/avatar.png"),
