    background-color: #f4f4f4;
    color: #000;
    font-family: "Tahoma";
}

.aboutus__header {
    text-align: center;
    margin-bottom: 30px;
    font-weight: 600;
}

.aboutus__title {
    font-weight: 700;
    font-size: 30px;
    margin-bottom: 20px;
}

.aboutus__suptitle {
    font-size: 24px;
    margin-bottom: 70px;
}

.aboutus__info {
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 25px;
}

.aboutus__info-bold {
    font-weight: 600;
}

.aboutus__items {
    display: flex;
    justify-content: space-between;
}

.aboutus__column {
    flex: 0 1 25%;
    margin: 0px 20px 0px 0px;
}
.aboutus__column:last-child {
    margin: 0px 0px 0px 0px;
}
.aboutus__column:last-child {
    margin-right: 0px;
}

.aboutus__item {
    text-align: center;
}

.aboutus__icon {
    margin-bottom: 30px;
    text-align: center;
    margin: 0px auto 30px auto;
}

.aboutus__icon {
    position: relative;
}

.circle {
    position: absolute;
    top: 0;
    left: 50%;
    z-index: 1;
    width: 50px;
    height: 50px;
    border-radius: 100%;
    background-color: #4a9ee7be;
}
.img {
    position: relative;
    z-index: 2;
}
.aboutus__subtitle {
    margin-bottom: 15px;
    font-weight: 600;
    font-size: 20px;
    color: #4a9ee7;
}

.aboutus__descr {
    font-size: 16px;
    line-height: 20px;
}
:root {
    --animate-duration: 1s;
    --animate-delay: 1s;
    --animate-repeat: 1;
}

.animate__animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-duration: var(--animate-duration);
    animation-duration: var(--animate-duration);
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;
}

@media (prefers-reduced-motion: reduce), print {
    .animate__animated {
        -webkit-animation-duration: 1ms !important;
        animation-duration: 1ms !important;
        -webkit-transition-duration: 1ms !important;
        transition-duration: 1ms !important;
        -webkit-animation-iteration-count: 1 !important;
        animation-iteration-count: 1 !important;
    }

